}


.statsdata {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}

.dataview {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
}

.dataview>span:nth-of-type(1) {
  font-weight: bold;
  font-size: 35px;
  color: #1877f2;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.dataview>span:nth-of-type(2) {
  font-size: 18px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}


